<template>
  <div>
        <div class="header">
            <el-button type="primary" size="mini">上传</el-button>
            <el-button icon="el-icon-full-screen" size="mini">全屏</el-button>
            <el-input placeholder="请输入内容" id="input"></el-input>
        </div>
        <!-- 显示数据 -->
        <el-table style="width: 100%" border :data="waterPumpData">
            <el-table-column type="index" label="系统" width="80" align="center"></el-table-column>
            <el-table-column prop="parameterType" label="参数类型" width="width"></el-table-column>
            <el-table-column prop="parameterId" label="参数ID" width="width"></el-table-column>
            <el-table-column prop="parameterName" label="参数名称" width="width"></el-table-column>
            <el-table-column prop="date" label="操作" width="width">
                    <el-button icon="el-icon-full-screen" size="mini" @click="isShow=true">数据可视化</el-button>
            </el-table-column>
        </el-table>
        <!-- 分页器 
            @size-change="handleSizeChange"
            @current-change="handleCurrentChange"
        -->
        <el-pagination
            style="text-align:center;margin-top:15px"
            :current-page="1"
            :page-sizes="[3,5,10]"
            :page-size="3"
            :total="dataTotal"
            layout="prev, pager, next, jumper,->, sizes, total">
        </el-pagination>
        <!-- 下边折线图抽屉 -->
        <el-drawer
            size="50%"
            direction="btt"
            :title="lineChartTitle"
            :visible.sync="isShow">
            <div style="background-color:#FFFFBB;width:100%;height:100%">
                <LineCharts :lineChartTitle="lineChartTitle"/>
            </div>
      </el-drawer>
  </div>
</template>

<script>
    import LineCharts from '@/components/LineCharts'

    export default {
        name:'DataTable',
        components:{
            LineCharts
        },
        data(){
            return {
                waterPumpData:[
                    {
                        system:'CW1',
                        parameterType:'---',
                        parameterId:'001',
                        parameterName:'dfff'
                    },
                    {
                        system:'CW1',
                        parameterType:'---',
                        parameterId:'001',
                        parameterName:'dfff'
                    },
                    {
                        system:'CW1',
                        parameterType:'---',
                        parameterId:'001',
                        parameterName:'dfff'
                    },
                    {
                        system:'CW1',
                        parameterType:'---',
                        parameterId:'001',
                        parameterName:'dfff'
                    },
                ],
                isShow:false
            }
        },
        computed:{
            dataTotal(){
                return this.waterPumpData.length
            },
            lineChartTitle(){
                return "系统名称:"+this.waterPumpData[0].system+"  参数ID:"+this.waterPumpData[0].parameterId+"  参数名称:"+this.waterPumpData[0].parameterName
            }
        },
        
    }
</script>

<style scope>
    .header{
        display: flex;
        margin-bottom: 10px;
    }
    #input{
        float:right;
        width: 150px;
    }
</style>