<template>
    <div ref="charts" class="charts">
    </div>
</template>

<script>
    import * as echarts from "echarts"

    export default {
        name:"LineCharts",
        props:{

        },
        mounted(){
            //初始化echarts实例
            let lineCharts = echarts.init(this.$refs.charts)
            //配置数据
            console.log(this.lineChartTitle)
            lineCharts.setOption({
                xAxis: {
                    type: 'category',
                    data: ['Mon', 'Tue', 'Wed', 'Thu', 'Fri', 'Sat', 'Sun']
                },
                yAxis: {
                    type: 'value'
                },
                series: [
                    {
                        data: [51, 130, 224, 500, 344, 256, 855],
                        type: 'line'
                    }
                ]
            })
        }
    }
</script>

<style>
    .charts{
        width: 100%;
        height: 100%;
    }
</style>